.App {
  text-align: center;
}

.link{
  background-color: purple;
  width: 200px;
  height: 60px;
  font-size: 23px;
  line-height: 60px;
  text-decoration: none;
  color: white;
  display: inline-block;
  border: 1px solid white;
}

.link2{
  color: white;
  text-decoration: none;
}

.link2:hover {
  color: goldenrod;
}

.link:hover {
  color: purple;
